.switch {
	display: flex;
	align-items: center;

	border-radius: 30px;
	background-color: var(--display);
	height: 20px;
	width: 50px;
	margin-left: 10px;
	cursor: pointer;
}

.switch1 {
	justify-content: flex-start;
}
.switch2 {
	justify-content: center;
}
.switch3 {
	justify-content: flex-end;
}

.circle {
	width: 15px;
	height: 15px;
	border-radius: 100%;
	background-color: var(--equalBorder);
	margin-left: 5px;
	margin-right: 5px;
}
