.button-clear {
	height: 80px;
	font-size: 1.6em;
	display: flex;
	flex: 1;
	background-color: var(--equal);
	margin-top: 8px;
	justify-content: center;
	align-items: center;
	color: white;
	border-bottom: 5px solid var(--equalBorder);
	cursor: pointer;
	border-radius: 20px;
	user-select: none;
}

.button-clear:hover {
	background-color: #ebb000;
}
