.button-container {
	height: 75px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-weight: bold;
	font-size: 38px;
	background-color: var(--numberkeys);
	color: var(--text);
	border-bottom: 5px solid var(--borderKeys);
	border-radius: 25px;
	cursor: pointer;
	user-select: none;
	margin: 4px;
	flex: 1 1;
}

.button-container:hover {
	font-size: 30px;
}

.operator {
	background-color: var(--delr);
	border-bottom: 5px solid var(--delrBorder);
}
