.input {
	height: 75px;
	border-radius: 50px;
	margin-bottom: 20px;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	font: bold;
	font-size: 30px;
	background-color: var(--display);
	padding: 11px 30px 11px 11px;
	margin: 1px solid #888;
	box-shadow: -1px 4px 1px 1px var(--borderKeys);
	color: var(--textop);
	overflow: hidden;
}
