.App {
	width: 100%;
	max-width: 100vw;
	height: 100vh;
	padding-top: 10px;
	background-color: var(--body);
	font-family: Lato, sans-serif;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

.calculator-container {
	width: 400px;
	height: 600px;
	max-width: 100vw;
	background-color: var(--calculator);
	border-radius: 20px;
	border: 5px solid var(--borderKeys);
	padding: 14px;
}

.row {
	margin: 4px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.theme {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: flex-end;
	margin-bottom: 10px;
	font-family: Lato, sans-serif;
	color: var(--text);
}

.color1 {
	--display: hsl(224, 36%, 15%);
	--body: hsl(222, 26%, 31%);
	--calculator: hsl(223, 31%, 20%);
	--numberkeys: hsl(30, 25%, 89%);
	--borderKeys: hsl(28, 16%, 65%);
	--delr: hsl(225, 21%, 49%);
	--delrBorder: hsl(224, 28%, 35%);
	--equal: hsl(6, 63%, 50%);
	--equalBorder: hsl(6, 70%, 34%);
	--text: hsl(221, 14%, 31%);
	--textspecial: white;
	--textop: white;
}

.color2 {
	--display: hsl(0, 0%, 93%);
	--body: hsl(0, 0%, 90%);
	--calculator: hsl(0, 5%, 81%);
	--numberkeys: hsl(30, 25%, 89%);
	--borderKeys: hsl(28, 16%, 65%);
	--delr: hsl(185, 42%, 37%);
	--delrBorder: hsl(185, 58%, 25%);
	--equal: hsl(25, 98%, 40%);
	--equalBorder: hsl(25, 99%, 27%);
	--text: hsl(60, 10%, 19%);
	--textspecial: white;
	--textop: black;
}

.color3 {
	--display: hsl(224, 36%, 15%);
	--body: hsl(268, 75%, 9%);
	--calculator: hsl(268, 71%, 12%);
	--numberkeys: hsl(268, 47%, 21%);
	--borderKeys: hsl(290, 70%, 36%);
	--delr: hsl(281, 89%, 26%);
	--delrBorder: hsl(285, 91%, 52%);
	--equal: hsl(176, 100%, 44%);
	--equalBorder: hsl(177, 92%, 70%);
	--text: hsl(52, 100%, 62%);
	--textspecial: white;
	--textop: hsl(52, 100%, 62%);
}
